import React from 'react';
import './App.min.css';
import Nav from './Nav.js';
import Plateau from './Plateau.js';
import Tableau from './Tableau.js';
import Scores from './Scores.js';
import Rules from './Rules.js';
import Start from './Start.js';
import Win from './Win.js';
import Loose from './Loose.js';
import Alert from './Alert.js';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        afficheScores: false,
        afficheRules: false,
        afficheWin: false,
        afficheLoose: false,
        afficheAlert: false,
        afficheNav: false,
        afficheGame: false,
        scores: 0,
        line: 0,
        round: 0,
        value: '',
        nbrLine: 12,
        nbrRound: 4,
        difficulty: 'difficultySimple'
    };

    this.state.compositions= this.random();

    this.state.game = Array(this.state.nbrLine).fill(undefined).map(e => Array(this.state.nbrRound).fill(undefined).map(e => ""));
    this.state.result = Array(this.state.nbrLine).fill(undefined).map(e => [0, 0]);

    this.toggleClick = this.toggleClick.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.recupValue = this.recupValue.bind(this);
    this.submitValue = this.submitValue.bind(this);
    this.undoClick = this.undoClick.bind(this);
    this.goClick = this.goClick.bind(this);
    this.compare = this.compare.bind(this);
    this.random = this.random.bind(this);
    this.difficulty = this.difficulty.bind(this);
    this.verifDif = this.verifDif.bind(this);
  }

  recupValue(event) {
    this.setState({
        value: event.target.value
    });
  }

  difficulty(difficulty) {
      this.setState(state => ({
          difficulty: difficulty
      }))
  }

  verifDif() {
      if (this.state.difficulty === 'difficultySimple') {
          this.setState(state => ({
              nbrLine: 12,
              nbrRound: 4,
          }))
      } else if (this.state.difficulty === 'difficultyNormal') {
          this.setState(state => ({
              nbrLine: 8,
              nbrRound: 4,
          }))
      } else if (this.state.difficulty === 'difficultyDifficile') {
          this.setState(state => ({
              nbrLine: 10,
              nbrRound: 6,
          }))
      }
  }

  submitValue(event) {
    if (this.state.value.length >= 2) {
      this.setState(state => ({
          afficheGame: !state.afficheGame
      }))
      this.verifDif();
    }
    event.preventDefault();
  }

  toggleClick(element) {
      this.setState(state => ({
          [element]: !state[element]
      }))
  }

  handleClick(color) {
      this.setState((state) => {
          let game = state.game;
          game[this.state.line][this.state.round] = color;
          return {round: this.state.round < 3 ? this.state.round + 1 : this.state.round,
              game: game};
      })
  }

  undoClick() {
      let round = ((this.state.round === 0) || ((this.state.round === (this.state.nbrRound-1)) && (this.state.game[this.state.line][this.state.round] !== "")) ) ? this.state.round : this.state.round - 1;
      this.setState((state) => {
          let game = state.game;
          if (game[state.line][state.round] === "") {
              game[state.line][state.round - 1] = "";
          } else {
              game[state.line][state.round] = "";
          }
          return {round: round, game: game}
      })
  }

  goClick() {
      if (this.state.game[this.state.line][this.state.round] === "") {
          this.toggleClick('afficheAlert');
          setTimeout(() => {
              this.toggleClick('afficheAlert');
          }, 1250)
      } else if (this.state.game[this.state.line][this.state.round + 1] === "") {
          this.toggleClick('afficheAlert');
          setTimeout(() => {
              this.toggleClick('afficheAlert');
          }, 1250)
      } else {
          if (this.state.line !== (this.state.nbrLine - 1)) {
              this.setState({
                  line: this.state.line + 1,
                  round: 0
              })
          }
          this.goodBadResult();
      }
  }

  goodBadResult() {
      let results = this.compare();
      if (results[0] === this.state.nbrRound || this.state.line === (this.state.nbrLine - 1)) {
          let win = results[0] === this.state.nbrRound;

          if (win) this.toggleClick('afficheWin');
          else this.toggleClick('afficheLoose');

          this.setState(state => {
              let s = win ? (state.scores + (state.nbrLine - state.line)) : state.scores - 2;
              return {
                line: 0,
                round: 0,
                compositions: this.random(),
                game: Array(this.state.nbrLine).fill(undefined).map(e => Array(this.state.nbrRound).fill(undefined).map(e => "")),
                result: Array(this.state.nbrLine).fill(undefined).map(e => [0, 0]),
                scores: s
              }
          })
      } else {
          this.setState(state => {
             let r = state.result;
             r[this.state.line] = results;
             return {result: r};
          });
      }
  }

  compare() {

      let tabCompo = [...this.state.compositions];

      let good = 0;
      let bad = 0;

      for (let i = 0; i < this.state.game[this.state.line].length; i++) {
          let choice = this.state.game[this.state.line][i];
          if (tabCompo.includes(choice)) {
              tabCompo[tabCompo.indexOf(choice)] = "";
              bad++;
          }
      }

      for (let i = 0; i < this.state.game[this.state.line].length; i++) {
          let choice = this.state.game[this.state.line][i];
          if (this.state.compositions[i] === choice) {
              good++;
              bad--;
          }
      }

      return [good, bad];
  }

  random() {
     let allColors = ["red","green","blue","purple","yellow","orange"];
     let randomColor=[];
     for (let i = 0; i < this.state.nbrRound; i++) {
       randomColor.push(allColors[Math.floor(Math.random() * allColors.length)]);
     }
     return randomColor;
  }


  render() {
    return (
        <div className="App">
          <Nav pseudo={this.state.value} scores={this.state.scores} toggleClick={this.toggleClick} afficheNav={this.state.afficheNav}/>
          <Loose scores={this.state.scores} toggleClick={this.toggleClick} afficheLoose={this.state.afficheLoose}/>
          <Scores scores={this.state.scores} toggleClick={this.toggleClick} afficheScores={this.state.afficheScores}/>
          <Win scores={this.state.scores} toggleClick={this.toggleClick} afficheWin={this.state.afficheWin}/>
          <Alert afficheAlert={this.state.afficheAlert}/>
          <Rules toggleClick={this.toggleClick} afficheRules={this.state.afficheRules}/>
          <Start difficultyFunc={this.difficulty} afficheGame={this.state.afficheGame} value={this.state.value} submitValue={this.submitValue} recupValue={this.recupValue} difficulty={this.state.difficulty}/>
          <main>
            <Tableau compare={this.compare} go={this.goClick} undo={this.undoClick} pseudo={this.state.value} clicked={this.handleClick} toggleClick={this.toggleClick}/>
            <Plateau nbrLine={this.state.nbrLine} nbrRound={this.state.nbrRound} round={this.state.round} line={this.state.line} resultColor={this.state.result} lineColor={this.state.game}/>
          </main>
        </div>
    );
  }
}
