import React from 'react';

export default class Start extends React.Component {

  render() {
    return (
      <section className={"Start" + (this.props.afficheGame ? " close" : "")}>
          <div>

              <form onSubmit={this.props.submitValue}>
                <label>
                  <input type="text" autoFocus value={this.props.value} onChange={this.props.recupValue} placeholder="Pseudo"/>
                </label>
                <input type="submit" value="Envoyer" />
              </form>

              <div>
                  <button className={this.props.difficulty === "difficultySimple" ? "active" : ""} type="button" onClick={() => this.props.difficultyFunc('difficultySimple')}>Simple</button>
                  <button type="button" className={this.props.difficulty === "difficultyNormal" ? "active" : ""} onClick={() => this.props.difficultyFunc('difficultyNormal')}>Normal</button>
                  <button type="button" className={this.props.difficulty === "difficultyDifficile" ? "active" : ""} onClick={() => this.props.difficultyFunc('difficultyDifficile')}>Difficile</button>
              </div>
          </div>
      </section>
    );
  }
}
